import React, { Component } from 'react';
import NavBar from './NavBar';

import globe from '../globe.svg'
import bottom from '../bottom.svg'
import lesson1 from '../lesson1.svg' 
import lesson2 from '../lesson2.svg'
import lesson3 from '../lesson3.svg'
import app from '../app.svg'
import yes from '../yes.svg'
import no from '../no.svg'
import done from '../done.svg'
import yogaApp from '../yogaApp.svg'
import App2 from '../app2.svg'
import App4 from '../app4.svg'

class Lesson1 extends Component {
    constructor(props) {
        super(props);
        if(localStorage.getItem("2question1") === null || localStorage.getItem("2question2") === null || localStorage.getItem("2question3") === null || localStorage.getItem("2question4") === null) {
            localStorage.setItem("2question1", 1);
            localStorage.setItem("2question2", 0);
            localStorage.setItem("2question3", 0);
            localStorage.setItem("2question4", 0);
        } 
        this.state = {
            question1: localStorage.getItem("2question1"),
            question2: localStorage.getItem("2question2"),
            question3: localStorage.getItem("2question3"),
            question4: localStorage.getItem("2question4"),
            showHint1: false,
            showHint2: false,
            showHint3: false,
            showHint4: false,
            showDone1: false,
            showDone2: false,
            showDone3: false,
            showDone4: false,
        };
      }

render(){
    return(
        <div className="mainpage">
            
            <NavBar></NavBar>
            
            <p className="homePageTitle">Internet Securtiy</p>
            <img className="globeLesson" src={globe}></img>

            <div className="bodyLesson">
                <br></br><br></br><br></br>
                <p className="homePageTitle">Mobile device security</p>
                <p className="normalText secondParagraph"> 
                Mobile devices became incredibly powerful in the last decade and now everybody is carying a little computer in their pocket that allows them to perform different actions from playing games to paying bills. By gaining all functionalities that were previously available only on computers, mobile phones also gained the same security vulnerabilities as computers and became a target of hackers. That is why we have to be careful with using our mobile devices same like with computers. There are 5 rules how to keep our mobile devices safe:
                </p>

                <p className="normalText secondParagraph"> 
                <b>PIN Code</b><br></br><br></br>
                Our mobile devices should always have a pin code or pattern set for unlocking the device. This won’t prevent robbery but it will protect the data on the device from being accessed like your private messages, pictures or online banking data. 
                </p>

                <p className="normalText secondParagraph"> 
                <b>In app purchases</b><br></br><br></br>
                It is important to set a separate pin code for in app purchases and disable auto checkout in device settings so the mobile device won’t remember your credit card details. This way if your mobile device is stolen or someone gained remote access over your device they won’t be able to make purchases for you.
                </p>

                <p className="normalText secondParagraph"> 
                <b>Find my device and remote data erase</b><br></br><br></br>
                Most mobile devices now come with in built services for finding the device and removing data in case the device is lost or stolen. Make sure that these options are enabled on your device or install apps for this in case your mobile device doesn’t support these functionalities by default. This will protect your data in case of a robbery or help you find your device if you lost it.
                </p>

                <p className="normalText secondParagraph"> 
                <b>Delete your private data before selling your phone </b><br></br><br></br>
                When you want to sell your device be aware that your data will still be on the phone and just deleting them isn’t enough as it can be restored with special software. You have to use software for permanently deleting data from the phone or use in build option for this if your mobile device has one.
                </p>

                <p className="normalText secondParagraph"> 
                <b>Update apps and OS</b><br></br><br></br>
                Make sure that your mobile device operating system is always up to date to be secure. Updates can also contain security patchers that can prevent hackers to use discovered security vulnerabilities in operating system of your mobile device, this is why it is important to always update your mobile device and the best option is to enable automatic updates in settings of your device. Same applies for applications installed on your device, if some application hasn’t been updated in years it is quite possible that it has some security vulnerabilities especially if it has access to your private data.
                </p>

                <p className="normalText secondParagraph"> 
                Beside these rules there is one more thing you need to do to be safe. Always check which permissions an app requires before installing it and allowing all permissions. Even if the app is installed from a trusted source like Goole Play or App Store it is not guaranteed that app won’t use those permissions in a malicious way. Any developer can create an account on these platforms and add new apps. Apps are not checked in detail what they do and they may try to steal your personal data like pictures or contacts. Before installing an app check the reviews and permissions if some of the permissions look suspicious don’t allow the app to have them, you can always enable them latter in settings. 
                </p>

                <iframe className="video" src="https://www.youtube.com/embed/7KuZ6cn6Now" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>    
            
            </div>

            <div className="bodyLesson">
                <br></br>
                <div className={this.state.question1 == 1 ? '' : 'hide'}>
                    <p className="questionNumber">Question 1/4:</p>
                    <p className="question">Select true statment:</p>
                    <div>
                        <p className="option optionfalse" onClick={ () => {this.setState(() => {return {showHint1: true};})}}>When you are seling your phone you don't have to wory about your data</p>
                    </div>
                    <div>
                        <p className="option optionfalse" onClick={ () => {this.setState(() => {return {showHint1: true};})}}>It is enough to just delet data like pictures and contacts</p>
                    </div>
                    <div>
                        <p className="option optiontrue" onClick={ () => {
                            this.setState(() => 
                                {return {showHint1: false, showDone1: true};
                                });
                            
                            setTimeout(() => {
                                localStorage.setItem("2question1", 0);
                                localStorage.setItem("2question2", 1);
                                this.setState(() => 
                                {return {question1: 0, question2: 1};
                                });
                            },2500);
                                }}>You have to use software for permanently deleting data from a phone or use in build option for this if your mobile device has one.</p>
                    </div>

                    <div className={this.state.showDone1 ? '' : 'hide'}>
                        <div className="correctMessage">
                        <p className="correctMessageText">Correct answer</p>
                        </div>
                    </div>
                    
                    <div className={this.state.showHint1 ? '' : 'hide'}>
                        <div className="hintMessage">
                            <p className="hintMessageText">Just deleting data isn't enough because it can be recoved with some tools. When seling a phone you should use software for permanently deleting data from the phone.</p>
                            <br></br>
                        </div>
                    </div>
                    
                </div>

                <div className={this.state.question2 == 1 ? '' : 'hide'}>
                    <p className="questionNumber">Question 2/4:</p>
                    <p className="question">Is this app safe to install?</p>
                    <div className="row">                   
                    <img className="appScreen" src={yogaApp}></img>   
                    </div>
                    <div className="row buttons justify-content-center justify-content-md-between">
                    <div onClick={ () => {
                            this.setState(() => 
                                {return {showHint2: false, showDone2: true};
                                });
                            
                            setTimeout(() => {
                                localStorage.setItem("2question2", 0);
                                localStorage.setItem("2question3", 1);
                                this.setState(() => 
                                {return {question2: 0, question3: 1};
                                });
                            },2500);
                                }}>
                        <img className="yesButton" src={yes}></img>
                    </div>
                    <div onClick={ () => {this.setState(() => {return {showHint2: true};})}}>
                        <img className="yesButton" src={no}></img>
                    </div>
                    
                    </div>

                    <div className={this.state.showDone2 ? '' : 'hide'}>
                    <div className="correctMessage">
                    <p className="correctMessageText">Correct answer</p>
                    </div>
                    </div>
                    
                    <div className={this.state.showHint2 ? '' : 'hide'}>
                    <div className="hintMessage">
                        <p className="hintMessageText">This app looks safe, it is updated, doesn't require unnecessary permissions and has good reviews   </p>
                        <br></br>
                    </div>
                    </div>
                    
                </div>

                <div className={this.state.question3 == 1 ? '' : 'hide'}>
                    <p className="questionNumber">Question 3/4:</p>
                    <p className="question">Is this app safe to install?</p>
                    <div className="row">                   
                    <img className="appScreen" src={App2}></img>   
                    </div>
                    <div className="row buttons justify-content-center justify-content-md-between">
                    <div onClick={ () => {this.setState(() => {return {showHint3: true};})}}>
                        <img className="yesButton" src={yes}></img>
                    </div>
                    <div onClick={ () => {
                            this.setState(() => 
                                {return {showHint3: false, showDone3: true};
                                });
                            
                            setTimeout(() => {
                                localStorage.setItem("2question3", 0);
                                localStorage.setItem("2question4", 1);
                                this.setState(() => 
                                {return {question3: 0, question4: 1};
                                });
                            },2500);
                                }}>
                        <img className="yesButton" src={no}></img>
                    </div>
                    
                    </div>

                    <div className={this.state.showDone3 ? '' : 'hide'}>
                        <div className="correctMessage">
                        <p className="correctMessageText">Correct answer</p>
                        </div>
                    </div>
                    
                    <div className={this.state.showHint3 ? '' : 'hide'}>
                        <div className="hintMessage">
                            <p className="hintMessageText">This app looks safe, but it is updated 3 years ago, it is possible that it has security flaws that can be missused, try to find an app with same functionalities that is updated recently</p>
                            <br></br>
                        </div>
                    </div>
                    
                </div>

                <div className={this.state.question4 == 1 ? '' : 'hide'}>
                    <p className="questionNumber">Question 4/4:</p>
                    <p className="question">Is this app safe to install?</p>
                    <div className="row">                   
                    <img className="appScreen" src={App4}></img>   
                    </div>
                    <div className="row buttons justify-content-center justify-content-md-between">
                    <div onClick={ () => {this.setState(() => {return {showHint4: true};})}}>
                        <img className="yesButton" src={yes}></img>
                    </div>
                    <div onClick={ () => {
                            this.setState(() => 
                                {return {showHint4: false, showDone4: true};
                                });
                            
                            setTimeout(() => {
                                localStorage.setItem("2question3", 0);
                                localStorage.setItem("2question4", 0);
                                this.setState(() => 
                                {return {question3: 0, question4: 0};
                                });
                            },2500);
                                }}>
                        <img className="yesButton" src={no}></img>
                    </div>
                    </div>

                    <div className={this.state.showDone4 ? '' : 'hide'}>
                        <div className="correctMessage">
                        <p className="correctMessageText">Correct answer</p>
                        </div>
                    </div>
                    
                    <div className={this.state.showHint4 ? '' : 'hide'}>
                        <div className="hintMessage">
                            <p className="hintMessageText">This app requires questinable permissions and has bad reviews</p>
                            <br></br>
                        </div>
                    </div>
                    
                </div>

                <div className={this.state.question4 == 0 && this.state.question3 == 0 && this.state.question2 == 0 && this.state.question1 ==0 ? '' : 'hide'}>
                    <div className="done">
                        <img className="doneMark" src={done}></img>
                        <p className="doneMessage text-center">Congratulations you have passed the test<br></br> Be safe when you use your mobile devices</p>
                    </div>
                </div>
                
            </div>

            


            <div className="navigation">
            <div className="row justify-content-center justify-content-md-between">
            <a className="navigationTab" href="/passwords"><img src={lesson1}></img></a>
            <a className="navigationTab" href="/mobileDevices"><img src={lesson2}></img></a>
            <a className="navigationTab"href="/phising"><img src={lesson3}></img></a>
            </div>
            </div>

            <div className="bottom">
                <img src={bottom}></img>
            </div>
        </div>

    
    );
}

}

export default Lesson1;